.admin-nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
  background-color: #21325e;
  color: white;
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
  font-size: 15px;
  height: 40px;
}

.admin-figure {
  display: flex;
  justify-content: stretch;
  background-color: rgb(255, 255, 255);
  height: 720px;
  width: 100%;
}

.admin-content {
  float: left;
  transition: 1s;
}

.admin-nav2 {
  float: right;
  background-color: #3e497a;
  display: block;
  transition: 1s;
}

.max-width {
  width: 15%;
}

.max-width2 {
  width: 85%;
}

.admin-nav2 ul {
  color: white;
  font-size: 17px;
  display: grid;
  justify-content: stretch;
  list-style: none;
  text-align: right;
}

.admin-nav2 ul li {
  margin-top: 20px;
  margin-bottom: 20px;
  width: 100%;
  cursor: pointer;
  transition: 0.3s;
  text-align: center;
}

.zero-width {
  width: 0%;
}

.zero-width2 {
  width: 100%;
}

.fa-bars:hover {
  cursor: pointer;
}

.menu-select {
  background-color: white;
  min-width: 15%;
  color: black;
  font-weight: bold;
}

@media (max-width: 900px) {
  .admin-content {
    min-width: 80%;
  }
  .max-width {
    width: 20%;
  }
}

@media (max-width: 650px) {
  .admin-content {
    min-width: 75%;
  }
  .max-width {
    width: 25%;
  }
  .admin-nav2 ul {
    font-size: 15px;
  }
}
