.nothing2 {
  color: rgb(211, 67, 67);
  font-weight: bold;
  text-align: center;
  background-color: rgb(241, 170, 170);
  width: 70%;
  margin: auto;
  margin-top: 40px;
  height: 30px;
  padding-bottom: 17px;
}

.admin-lecture {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  padding-top: 10px;
  width: 60%;
  border-bottom: 1px solid;
  margin: auto;
  height: 120px;
  overflow-y: auto;
  direction: rtl;
}
.lesson-added {
  direction: rtl;
  text-align: right;
  padding-right: 100px;
  padding-top: 10px;
}
.admin-lecture div {
  width: 30%;
  height: 80px;
  text-align: center;
  background-color: #3e497a;
  color: white;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  border-radius: 20px;
  cursor: pointer;
  transition: 0.5s;
}

.admin-lecture div:hover {
  background-color: #313c69;
}

.video {
  width: 100%;
  display: flex;
  justify-content: center;
  align-content: center;
}
.video video {
  text-align: center;
  width: 600px;
  height: 300px;
  margin-top: 30px;
}

@media (max-width: 800px) {
  .lesson-added {
    padding-right: 30px;
    font-size: 18px;
  }

  .admin-lecture div {
    font-size: 15px;
    width: 170px;
  }

  .video video {
    width: 220px;
    height: 200px;
  }
}
