.admin-main {
  display: flex;
  width: 70%;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: auto;
  margin-top: 60px;
  height: 600px;
}

.admin-main div {
  width: 400px;
  border: none;
  border-top: none;
  min-height: 90px;
  max-height: 100px;
  display: grid;
  padding-top: 0;
  align-items: center;
}

.admin-main .admin-main-first {
  width: 800px;
  margin: auto;
  margin-top: 0;
  margin-bottom: 0;
  overflow-y: auto;
}

.admin-main .admin-main-last {
  overflow-y: auto;
  width: 800px;
  margin: auto;
  margin-top: 0;
  margin-bottom: 0;
}
/* .admin-main div:first-child{
    background-color: ;
  } */

.admin-main div h3 {
  text-align: center;
  height: 100%;
  background-color: #f1d00a;
  color: black;
}

.admin-main div ul {
  list-style: none;
  display: flex;
  justify-content: space-between;
  padding-left: 15px;
  padding-right: 15px;
  direction: rtl;
  width: 100%;
  text-align: center;
}

.admin-main div ul li {
  display: flex;
  justify-content: center;
  align-items: center;
  /* padding-left: 15px; */
  /* padding-right: 15px; */
  /* direction: rtl; */
  width: 100%;
  text-align: center;
}

.testo {
  /* border-bottom: 1px solid rgb(71, 71, 71); */
  background-color: #3e497a;
  color: white;
  /* font-weight: bold; */
  /* border-top: rgb(111, 169, 207); */
}

.testo2 {
  /* border-bottom: 1px solid rgb(71, 71, 71); */
  background-color: #f0f0f0;
  color: rgb(0, 0, 0);
  font-weight: bold;
  /* border-top: rgb(111, 169, 207); */
}

.nothing {
  color: rgb(211, 67, 67);
  font-weight: bold;
  text-align: center;
  background-color: rgb(241, 170, 170);
  width: 100%;
  height: 60%;
  padding-bottom: 17px;
}

.fa-check-circle {
  color: green;
  font-size: 22px;
  cursor: pointer;
  vertical-align: middle;
}

.fa-check-circle:hover {
  color: rgb(0, 110, 0);
}
.fa-times-circle {
  color: red;
  font-size: 22px;
  cursor: pointer;
  vertical-align: middle;
}
.fa-times-circle:hover {
  color: rgb(197, 0, 0);
}

@media (max-width: 700px) {
  .admin-main {
    width: 90%;
  }
  .testo {
    font-size: 13px;
  }
  .testo2 {
    font-size: 13px;
  }
  .fa-check-circle {
    font-size: 18px;
  }
  .fa-times-circle {
    font-size: 18px;
  }
}
