.nothing2 {
  color: rgb(211, 67, 67);
  font-weight: bold;
  text-align: center;
  background-color: rgb(241, 170, 170);
  width: 70%;
  margin: auto;
  margin-top: 40px;
  height: 30px;
  padding-bottom: 17px;
}

.stu-lecture {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  padding-top: 60px;
  width: 60%;
  border-bottom: 1px solid;
  margin: auto;
  height: 300px;
  overflow-y: scroll;
  direction: rtl;
}
.lesson-added {
  direction: rtl;
  text-align: right;
  padding-right: 100px;
  padding-top: 10px;
}
.stu-lecture div {
  width: 30%;
  height: 80px;
  text-align: center;
  background-color: rgb(219, 219, 219);
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
}

.video {
  width: 100%;
  display: flex;
  justify-content: center;
  align-content: center;
}
.video video {
  text-align: center;
  width: 600px;
  height: 300px;
  margin-top: 30px;
}
