.login {
  display: flex;
  justify-content: stretch;
  margin: auto;
  width: 100%;
  height: 690px;
  direction: ltr;
}

.login form {
  margin: auto;
  width: 40%;
  height: 80%;
  align-items: center;
  display: flex;
}
.login-div {
  width: 100%;
  background-color: #3e497a;
  /* border-bottom-right-radius: 100vh; */
  display: grid;
  text-align: center;
  justify-content: center;
  padding-top: 80px;
}

.login-div article h1 {
  color: white;
}

.login-div article p {
  color: white;
  padding-top: 50px;
}
.login form div {
  margin: auto;
  text-align: center;
  display: grid;
  direction: rtl;
  width: 80%;
  height: 60%;
  margin: auto;
  align-items: center;
}

/* .login form div {
} */

.login form div input {
  height: 40px;
  width: 100%;
  padding-right: 20px;
  color: black;
  font-weight: bold;
  outline: none;
  border: none;
  border: 1px solid #3e497a;
  border-radius: 20px;
}

.omega {
  width: 20px;
  height: 60%;
}
.omega:checked {
  color: wheat;
}

.login form div button {
  background-color: #3e497a;
  color: white;
  border: none;
  cursor: pointer;
  width: 100%;
  transition: 0.5s;
  border-radius: 20px;
}

.login form div button:hover {
  background-color: #3e497a;
}

.stu-tech {
  display: flex;
  /* border: 1px solid; */
  justify-content: space-evenly;
}

.login form div button {
  height: 40px;
}

.login form div h5 a {
  transition: 0.4s;
}
.login form div h5 a:hover {
  color: #3e497a;
}

@media (max-width: 1200px) {
  footer {
    margin-top: 0;
  }
}

@media (max-width: 768px) {
  .login {
    display: flex;
    /* justify-content: stretch; */
    width: 100%;
  }
  .login-div {
    width: 50%;
  }
  .login form div {
    width: 100%;
  }
  footer {
    margin-top: 0;
  }
}

/* loader*/
.lds-facebook {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  padding-right: 80px;
  padding-bottom: 60px;
}
.lds-facebook div {
  position: relative;
  left: 8px;
  width: 10px;
  background: #000000;
  animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
  box-shadow: 5px 2px 5px #21325e;
}
.lds-facebook div:nth-child(1) {
  left: 8px;
  animation-delay: -0.24s;
}
.lds-facebook div:nth-child(2) {
  left: 32px;
  animation-delay: -0.12s;
}
.lds-facebook div:nth-child(3) {
  left: 56px;
  animation-delay: 0;
}
@keyframes lds-facebook {
  0% {
    top: 8px;
    height: 64px;
  }
  50%,
  100% {
    top: 24px;
    height: 32px;
  }
}

@media (max-width: 567px) {
  .login {
    display: grid;
    justify-content: stretch;
    width: 100%;
  }
  .lds-facebook {
    padding-right: 60px;
  }
  .login-div {
    width: 100%;
    border-bottom-left-radius: 10vh;
    border-bottom-right-radius: 10vh;
    height: 300px;
  }
  .login form div {
    width: 90%;
  }
  .login form {
    width: 90%;
    height: 500px;
  }
  footer {
    margin-top: 270px;
  }
}

.login-err {
  color: rgb(179, 46, 46);
  background-color: rgb(224, 137, 137);
  height: 30px;
  align-items: center;
  vertical-align: middle;
  font-weight: bold;
}
.login-normal {
  height: 30px;
  align-items: center;
  vertical-align: middle;
  font-weight: bold;
}
