.admin-task {
  display: grid;
  direction: rtl;
  justify-content: stretch;
  text-align: center;
  align-content: flex-start;
  width: 80%;
  margin: auto;
  margin-top: 10px;
  height: 100px;
  overflow-y: auto;
  /* border: 1px solid; */
}

.admin-task2 {
  display: flex;
  justify-content: space-between;
  background-color: #f1d00a;
  color: rgb(0, 0, 0);
  padding-right: 10px;
  height: 30px;
  padding-left: 180px;
}

.admin-task3 {
  display: flex;
  justify-content: space-between;
  background-color: #3e497a;
  color: rgb(255, 255, 255);
  padding-right: 10px;
  height: 30px;
}

.admin-task3 p input {
  padding-right: 10px;
  outline: none;
  border-radius: 20px;
  width: 250px;
  height: 20px;
  margin: auto;
  border: none;
  font-weight: bold;
}

.admin-apload {
  color: rgb(0, 0, 0);
  cursor: pointer;
  width: 200px;
  margin: auto;
  background-color: #f1d00a;
  font-weight: bold;
  border: none;
  height: 30px;
  border-radius: 20px;
  transition: 0.5s;
  margin-top: 10px;
  text-align: center;
}

.div-apload {
  display: flex;
  justify-content: center;
  align-items: center;
}

.admin-apload:hover {
  background-color: #dfbe08;
}

.admin-task-add {
  text-align: right;
  padding-right: 100px;
  margin-top: 20px;
}

@media (max-width: 830px) {
  .admin-task {
    width: 90%;
  }
  .admin-task3 p input {
    width: 150px;
  }
  .admin-task-add {
    padding-right: 20px;
    font-size: 18px;
  }
}

@media (max-width: 630px) {
  .admin-task2 {
    padding-left: 0px;
    padding-right: 0px;
    justify-content: space-evenly;
  }
  .admin-task3 p {
    width: 150px;
  }
  .admin-task3 input {
    width: 150px;
  }
}

@media (max-width: 500px) {
  .admin-task {
    display: flex;
    height: 150px;
  }
  .admin-task2 {
    display: grid;
    width: 175px;
    height: 150px;
    justify-content: center;
    align-items: center;
  }
  .admin-task3 {
    display: grid;
    width: 175px;
    justify-content: center;
    align-items: center;
    height: 150px;
  }
}
