.stu-task {
  display: grid;
  direction: rtl;
  justify-content: stretch;
  margin-top: 50px;
  text-align: center;
  align-content: flex-start;
  width: 80%;
  margin: auto;
  margin-top: 50px;
  height: 200px;
  overflow-y: scroll;
  /* border: 1px solid; */
}

.stu-task2 {
  display: flex;
  justify-content: space-between;
  background-color: #f1d00a;
  color: rgb(0, 0, 0);
  padding-right: 10px;
  height: 30px;
  width: 100%;
  font-weight: bold;
}

.stu-task2 p {
  width: 150px;
}

.stu-task3 {
  display: flex;
  justify-content: space-between;
  background-color: #f0f0f0;
  color: rgb(0, 0, 0);
  height: 50px;
  align-items: center;
  width: 100%;
  font-weight: bold;
}

.stu-task3 p {
  width: 150px;
}
.stu-task3 input {
  width: 150px;
}
.stu-apload {
  background-color: #f1d00a;
  border: 1px solid #f1d00a;
  width: 150px;
  border-radius: 20px;
  cursor: pointer;
  color: rgb(0, 0, 0);
  font-size: 17px;
  transition: 0.5s;
  font-weight: bold;
}

.stu-apload:hover {
  background-color: #d4b607;
}
