.registration {
  display: flex;
  justify-content: space-between;
  margin: auto;
  width: 100%;
  height: 690px;
  direction: rtl;
}
.rp p {
  margin-top: 0px;
  height: 30px;
}
.thing {
  color: rgb(36, 173, 93);
  font-weight: bold;
  text-align: center;
  background-color: rgb(170, 241, 170);
  width: 70%;
  margin: auto;
  margin-top: 40px;
  height: 30px;
  padding-bottom: 17px;
}
.registration-div {
  width: 70%;
  background-color: #3e497a;
  /* border-bottom-right-radius: 100vh; */
  display: grid;
  text-align: center;
  justify-content: center;
  padding-top: 80px;
}

.registration-div article h1 {
  color: white;
}

.registration-div article p {
  color: white;
  padding-top: 50px;
}
.registration form {
  margin: auto;
  text-align: center;
  display: grid;
  direction: rtl;
  width: 20%;
  height: 90%;
  margin: auto;
}

.registration form input {
  height: 40px;
  width: 100%;
  padding-right: 20px;
  color: black;
  font-weight: bold;
  outline: none;
  border: none;
  border: 1px solid #3e497a;
  border-radius: 20px;
}

.omega {
  width: 20px;
  height: 60px;
}
.omega:checked {
  color: wheat;
}

.registration form button {
  background-color: #3e497a;
  color: white;
  border: none;
  cursor: pointer;
  width: 100%;
  transition: 0.5s;
  border-radius: 20px;
}

.registration form button:hover {
  background-color: #2e3868;
}

.stu-tech {
  display: flex;
  /* border: 1px solid; */
  justify-content: space-evenly;
}

.registration form button {
  height: 40px;
}

.registration form h5 a {
  transition: 0.4s;
}
.registration form h5 a:hover {
  color: #3e497a;
}

.footer {
  background-color: #f0f0f0;
  height: 30px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 1200px) {
  footer {
    margin-top: 0;
  }
}

@media (max-width: 768px) {
  .registration {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  .registration-div {
    width: 50%;
  }
  .registration form {
    width: 40%;
  }
  footer {
    margin-top: 0;
  }
  .second-ul {
    margin: auto;
  }
}

@media (max-width: 567px) {
  .registration {
    display: grid;
    justify-content: stretch;
    width: 100%;
  }
  .registration-div {
    width: 100%;
    border-bottom-left-radius: 10vh;
    border-bottom-right-radius: 10vh;
    height: 150px;
  }
  .registration form {
    width: 90%;
    height: 500px;
  }
  footer {
    margin-top: 270px;
  }
}
