.admin-sent {
  margin: auto;
  display: grid;
  width: 85%;
  justify-content: stretch;
  direction: rtl;
  margin-top: 60px;
  overflow-x: auto;
  overflow-y: auto;
  height: 210px;
  grid-auto-rows: min-content;
}

.sent {
  width: 100%;
  display: flex;
  list-style: none;
  justify-content: space-between;
  margin: auto;
  background-color: #f1d00a;
}

.sent li {
  width: 200px;
  text-align: center;
  color: rgb(0, 0, 0);
  font-weight: bold;
}

.sent2 {
  width: 100%;
  display: flex;
  list-style: none;
  justify-content: space-between;
  margin: auto;
  margin-bottom: 20px;
  background-color: #f0f0f0;
  align-items: center;
}

.sent2 li {
  width: 200px;
  text-align: center;
  color: black;
  font-weight: bold;
  background-color: #f0f0f0;
  height: 30px;
}

.sent2 li button {
  height: 100%;
  width: 150px;
  border: none;
  background-color: darkcyan;
  color: white;
  transition: 0.6s;
  cursor: pointer;
}

.sent2 li button:hover {
  background-color: rgb(0, 124, 124);
}
