nav {
  display: flex;
  width: 100%;
  justify-content: right;
  padding-right: 30px;
  padding-left: 30px;
  background-color: #21325e;
  align-items: center;
  height: 40px;
}

/* first-ul */

nav .first-ul {
  background-color: #6c63ff;
  top: 39px;
  left: 0;
  position: absolute;
  width: 100%;
  display: grid;
  text-align: center;
  list-style: none;
  height: 2px;
  animation: name duration timing-function delay iteration-count direction
    fill-mode;
  animation-name: increasingHight;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  z-index: 10;
}

@keyframes increasingHight {
  from {
    height: 2px;
  }
  to {
    height: 220px;
  }
}

nav .first-ul li {
  /* margin-top:10px ; */
  /* margin-bottom:10px ; */
  font-size: 20px;
  color: white;
  cursor: pointer;
  transition: 0.3s;
  border-top: 1px solid white;
  width: 100%;
  padding-top: 9px;
}

nav .first-ul li:hover {
  color: #b0acff;
}

/* second-ul */
nav .second-ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
}

nav .second-ul li {
  margin-right: 20px;
  margin-left: 20px;
  font-size: 16px;
  color: white;
  border-right: 2px solid white;
  padding-right: 10px;
  cursor: pointer;
  transition: 0.3s;
}

nav .second-ul li:last-child {
  border-right: none;
}

nav .second-ul li:hover {
  color: #b9b9b9;
  border-color: #b9b9b9;
}

nav div h1 {
  color: white;
  font-weight: bold;
  letter-spacing: 20px;
  cursor: pointer;
}

nav div {
  text-align: right;
  width: 30%;
}

nav i {
  font-size: 28px;
  color: white;
  cursor: pointer;
}

nav i:hover {
  color: #b0acff;
}

@media (max-width: 567px) {
  nav {
    justify-content: space-between;
    padding-right: 10px;
    padding-left: 10px;
  }
  nav div h1 {
    letter-spacing: 8px;
  }
}

