.testo-exam {
  /* border-bottom: 1px solid rgb(71, 71, 71); */
  background-color: #f1d00a;
  color: rgb(0, 0, 0);
  font-weight: bold;
  /* border-top: rgb(111, 169, 207); */
  display: flex;
  justify-content: space-evenly;
  list-style: none;
  width: 100%;
  margin: auto;
  direction: rtl;
  text-align: center;
}

.testo-exam li {
  text-align: center;
  width: 150px;
}

.testo2-exam {
  /* border-bottom: 1px solid rgb(71, 71, 71); */
  background-color: #f0f0f0;
  color: rgb(0, 0, 0);
  font-weight: bold;
  /* border-top: rgb(111, 169, 207); */
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  margin: auto;
  list-style: none;
  direction: rtl;
  text-align: center;
  height: auto;
  align-items: center;
}

.testo2-exam li input {
  width: 100%;
  outline: none;
  border: none;
  padding-right: 10px;
  font-weight: bold;
  border: 1px solid;
}

.testo2-exam li {
  text-align: center;
  width: 150px;
}
.available-exam {
  text-align: right;
  direction: rtl;
  padding-right: 40px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.testo2-exam-btn {
  background-color: #f1d00a;
  border: 1px solid #f1d00a;
  width: 150px;
  border-radius: 20px;
  cursor: pointer;
  color: rgb(0, 0, 0);
  font-size: 17px;
  transition: 0.5s;
}

.testo2-exam-btn:hover {
  background-color: #daba08;
}

.testo2-exam-btn2 {
  text-align: center;
  width: 150px;
  border-radius: 20px;
  margin: auto;
  justify-self: center;
  cursor: pointer;
  background-color: #f1d00a;
  border: none;
  color: rgb(0, 0, 0);
  font-weight: bold;
  font-size: 18px;
  transition: 0.5s;
}

.testo2-exam-btn2:hover {
  background-color: #d8b90b;
}

.admin-exam {
  display: grid;
  margin: auto;
  width: 85%;
  overflow-x: auto;
  overflow-y: auto;
  height: 210px;
  grid-auto-rows: min-content;
}

.auto-hight {
  height: auto;
}

.btn-2 {
  margin: auto;
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  align-items: center;
  margin-bottom: 10px;
}

.btn-2 button {
  text-align: center;
  width: 150px;
  border-radius: 20px;
  margin: auto;
  justify-self: center;
  cursor: pointer;
  background-color: #f1d00a;
  border: none;
  color: rgb(0, 0, 0);
  font-weight: bold;
  font-size: 18px;
  transition: 0.5s;
}

.btn-2 :last-child {
  background-color: #f1d00a;
  border: 1px solid #f1d00a;
  width: 150px;
  border-radius: 20px;
  cursor: pointer;
  color: rgb(0, 0, 0);
  font-size: 17px;
  transition: 0.5s;
}

.btn-2 :last-child:hover {
  background-color: #d4b60b;
  border: 1px solid #d4b60b;
}
