.admin-degree {
  display: grid;
  direction: rtl;
  justify-content: stretch;
  margin-top: 50px;
  text-align: center;
  align-content: flex-start;
  width: 80%;
  margin: auto;
  margin-top: 50px;
  height: 200px;
  overflow-y: scroll;
}

.admin-degree-main {
  display: flex;
  list-style: none;
  justify-content: space-between;
  background-color: rgb(58, 58, 58);
  color: white;
  height: 30px;
}

.admin-degree-sub {
  list-style: none;
  border-bottom: 1px solid rgb(241, 241, 241);
  display: flex;
  justify-content: space-between;
  background-color: rgb(219, 219, 219);
  color: rgb(0, 0, 0);
  height: 30px;
}
.admin-degree-main li {
  width: 20%;
}

.admin-degree-sub li {
  width: 20%;
}

.admin-degree-last {
  display: grid;
  direction: rtl;
  justify-content: stretch;
  align-content: flex-start;
  width: 70%;
  margin: auto;
  margin-top: 50px;
  height: 150px;
  overflow-y: auto;
}

.admin-degree-last h3 {
  text-align: center;
  background-color: #f1d00a;
  color: rgb(0, 0, 0);
  font-size: 20px;
  width: 100%;
}

.admin-degree-last ul {
  display: flex;
  direction: rtl;
  justify-content: center;
  list-style: none;
}

.admin-degree-last2 {
  display: grid;
  direction: rtl;
  justify-content: stretch;
  align-content: flex-start;
  width: 70%;
  margin: auto;
  margin-top: 50px;
  height: 100px;
  overflow-y: auto;
}

.admin-degree-last2 h3 {
  text-align: center;
  background-color: #f1d00a;
  color: rgb(0, 0, 0);
  font-size: 20px;
  width: 100%;
}

.admin-degree-last2 ul {
  display: flex;
  direction: rtl;
  justify-content: center;
  list-style: none;
  height: 30px;
}

.admin-degree-last2 ul li {
  text-align: center;
  width: 200px;
}

.admin-degree-last2 ul li input {
  width: 100%;
  color: black;
  font-weight: bold;
  outline: none;
  height: 25px;
  padding-right: 10px;
}
.add-stu {
  width: 200px;
  height: 30px;
  border-radius: 20px;
  margin-top: 10px;
  background-color: #f1d00a;
  color: rgb(0, 0, 0);
  font-weight: bold;
  border: none;
  cursor: pointer;
  transition: 0.6s;
}

.add-stu:hover {
  background-color: #ddbd07;
}

.admin-degree2 {
  display: grid;
  direction: rtl;
  justify-content: stretch;
  margin-top: 50px;
  text-align: center;
  align-content: flex-start;
  width: 70%;
  margin: auto;
  margin-top: 50px;
  height: 150px;
  overflow-y: auto;
  background-color: rgb(221, 221, 221);
}

.admin-degree-main2 {
  display: flex;
  list-style: none;
  justify-content: center;
  background-color: rgb(95, 95, 95);
  color: white;
  height: 30px;
}

.admin-degree-main2 li {
  text-align: center;
  width: 50%;
}
.admin-degree-sub2 {
  list-style: none;
  border-bottom: 1px solid rgb(0, 0, 0);
  display: flex;
  justify-content: center;
  background-color: rgb(78, 78, 78);
  color: rgb(255, 255, 255);
  height: 30px;
  margin-bottom: 100px;
}

.admin-degree-sub2 li {
  text-align: center;
  width: 50%;
}

.admin-degree2 P {
  font-size: 26px;
  text-align: center;
  color: rgb(43, 43, 43);
  font-weight: bold;
}

.admin-degree2 h1 {
  color: white;
  background-color: rgb(105, 105, 105);
}

.admin-degree-main3 {
  display: flex;
  list-style: none;
  justify-content: center;
  background-color: #3e497a;
  color: white;
  height: 30px;
  width: 100%;
}
.fix-main3 {
  overflow-x: auto;
  width: 100%;
  display: grid;
}

.admin-degree-sub3 {
  list-style: none;
  border-bottom: 1px solid #f0f0f0;
  display: flex;
  justify-content: center;
  background-color: #f0f0f0;
  color: rgb(0, 0, 0);
  height: 30px;
  width: 100%;
}

.admin-degree-sub3 li {
  text-align: center;
  min-width: 200px;
}

.admin-degree-main3 li {
  text-align: center;
  min-width: 200px;
}

.admin-degree3 {
  display: grid;
  direction: rtl;
  justify-content: stretch;
  text-align: center;
  align-content: flex-start;
  width: 70%;
  margin: auto;
  margin-top: 50px;
  height: 150px;
  overflow-y: auto;
}

.admin-degree3 h1 {
  font-size: 20px;
  margin: auto;
  background-color: #f1d00a;
  width: 100%;
}

.admin-degree-last ul li {
  text-align: center;
  width: 33.3%;
}

@media (max-width: 700px) {
  .admin-degree-sub3 {
    font-size: 13px;
  }
}
