.testo-exam {
  /* border-bottom: 1px solid rgb(71, 71, 71); */
  background-color: #f1d00a;
  color: rgb(0, 0, 0);
  font-weight: bold;
  /* border-top: rgb(111, 169, 207); */
  display: flex;
  justify-content: space-evenly;
  list-style: none;
  width: 100%;
  margin: auto;
  direction: rtl;
  text-align: center;
}

.testo-exam li {
  text-align: center;
  width: 150px;
}

.testo2-exam {
  /* border-bottom: 1px solid rgb(71, 71, 71); */
  background-color: rgb(224, 224, 224);
  color: rgb(0, 0, 0);
  font-weight: bold;
  /* border-top: rgb(111, 169, 207); */
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  margin: auto;
  list-style: none;
  direction: rtl;
  text-align: center;
}

.testo2-exam li {
  text-align: center;
  width: 150px;
}
.available-exam {
  text-align: right;
  direction: rtl;
  padding-right: 40px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.testo2-exam-btn {
  background-color: #f1d00a;
  border: 1px solid #f1d00a;
  width: 150px;
  border-radius: 20px;
  cursor: pointer;
  color: rgb(0, 0, 0);
  font-size: 17px;
  transition: 0.5s;
  font-weight: bold;
  margin-top: 10px;
}

.testo2-exam-btn:hover {
  background-color: #f1d00a;
}
.stu-exam {
  display: grid;
}
