.admin-test2 {
  display: grid;
  height: 500px;
  border: none;
  margin: auto;
  background-color: #f0f0f0;
  margin-top: 10px;
  overflow-x: scroll;
  width: 100%;
}
.admin-test {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin: auto;
  justify-content: space-between;
  direction: rtl;
  list-style: none;
}

.admin-test h1 {
  text-align: center;
  width: 100%;
  margin-top: 20px;
}

.admin-test li {
  width: 80%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 20px;
}
.admin-test li input {
  width: 320px;
  border: none;
  font-size: 20px;
  font-weight: bold;
  border-top-left-radius: 10%;
  border-bottom-left-radius: 10%;
  height: 40px;
  color: rgb(100, 100, 100);
  outline: none;
  padding-right: 10px;
}
.admin-test li i {
  font-size: 30px;
  margin-left: 10px;
  max-width: 30px;
  vertical-align: middle;
  cursor: pointer;
  color: white;
}

.admin-test :first-child {
  width: 100%;
}

/* .admin-test :first-child input {
  width: 90%;
} */

.admin-test-save {
  text-align: center;
  font-size: 20px;
  background-color: #f1d00a;
  border-radius: 20px;
  cursor: pointer;
  border: none;
  color: rgb(0, 0, 0);
  margin: auto;
  margin-top: 30px;
  font-weight: bold;
  width: 150px;
  transition: 0.6s;
}
.admin-test-save:hover {
  background-color: #cfb108;
}
