.stu-nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
  background-color: #21325e;
  color: rgb(255, 255, 255);
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
  height: 40px;
}

.stu-figure {
  display: flex;
  width: 100%;
  height: 720px;
}

.stu-content {
  float: left;
  width: 75%;
  background-color: rgb(241, 241, 241);
  transition: 1s;
}

.stu-nav2 {
  float: right;
  width: 15%;
  background-color: #3e497a;
  transition: 1s;
}

.stu-nav2 ul {
  color: white;
  font-size: 17px;
  display: grid;
  list-style: none;
  text-align: center;
}

.stu-nav2 ul li {
  margin-top: 20px;
  margin-bottom: 20px;
  width: 100%;
  cursor: pointer;
  transition: 0.3s;
}

@media (max-width: 650px) {
  .stu-content {
    min-width: 75%;
  }
  .stu-nav {
    font-size: 13px;
    padding-left: 10px;
    padding-right: 30px;
  }
  .stu-nav2 ul li {
    font-size: 14px;
  }
}

@media (max-width: 400px) {
  .stu-nav {
    font-size: 11px;
  }
}
