.stu-degree {
  display: grid;
  direction: rtl;
  justify-content: stretch;
  margin-top: 50px;
  text-align: center;
  align-content: flex-start;
  width: 80%;
  margin: auto;
  margin-top: 50px;
  height: 200px;
  overflow-y: auto;
}

.stu-degree-main {
  display: flex;
  list-style: none;
  justify-content: space-between;
  background-color: #f1d00a;
  color: rgb(0, 0, 0);
  height: 30px;
  font-weight: bold;
}

.stu-degree-main li {
  width: 200px;
}
.stu-degree-sub {
  list-style: none;
  border-bottom: 1px solid #f0f0f0;
  display: flex;
  justify-content: space-between;
  background-color: #f0f0f0;
  color: rgb(0, 0, 0);
  height: 30px;
}

.stu-degree-sub li {
  width: 200px;
}
